/* Keyframes */
@keyframes parallax {
	from {
		opacity: 0;
	}
	50% {
		opacity: 1;
	}
	to {
		opacity: 0;
	}
}
@function multiple-box-shadows($stars, $size, $color){
	$box-shadow: random(100)+'vw ' + random(100)+'vh ' + $color;
	@for $i from 1 through $stars {
		 $box-shadow: $box-shadow + ', ' + random(100)+'vw ' + random(100)+'vh ' + $color;
	}
	@return unquote($box-shadow);
}
@mixin parallax-layer($stars, $size: 1px, $timer: 2(random(10) * 1s) + 10s, $color: white, $easing: linear){
	$shadows: multiple-box-shadows($stars, $size, $color);
	position: absolute;
	top: 0;
	left: 0;
	width: $size;
	height: $size;
	background: transparent;
	pointer-events: none;
	box-shadow: $shadows;
	animation: parallax $timer $easing infinite;
	border-radius: 50%;
	&:after {
		content: " ";
		position: absolute;
		width: $size;
		height: $size;
		top: 100vh;
		box-shadow: $shadows;
	}
}

.landing {
	position: absolute;
	display: flex;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	background: rgba($dark, 0.25);
	color: $light;
	overflow: hidden;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	z-index: 1;
}
.landing-canvas {
	position: absolute;
	left: 0;
	top: 0;
	bottom: 0;
	right: 0;
	z-index: 0;
}
.call-out-container {
	position: relative;
	z-index: 2;
}
.call-out, .action a{
	background: rgba($dark, 0.75);
}
.call-out {
	padding: 24px;
	h1 {
		font-size: 36px;
	}
	h2 {
		font-size: 24px;
	}
	.em {
		color: $red;
	}
}
.action {
	width: 100%;
	display: inline-flex;
	justify-content: space-around;
	a {
		padding: 12px;
		@include transition(color);
		color: $red;
		font-weight: bold;
		display: inline-flex;
		align-items: center;
		svg {
			margin: 0 6px;
		}
		&:hover {
			color: $light;
			background: $dark;
		}
	}
}
.landing-fill {
	margin-top: 100vh;
}
